<template>
    <div class="category">
         <dl class="m-categroy">
             <dt>按拼音字母选择：</dt>
             <dd v-for="(item,index) in list" :key="index">
                 <a :href="'#city-'+ item">
                     {{item}}
                 </a>
             </dd>            
         </dl>
          <dl class="m-categroy-section" v-for="(item,index) in cityGroup" :key="index" :id="'#city-'+ index">
                 <dt>{{index}}</dt>
                 <dd >
                     <span v-for="city in item" :key=city.id @click="changeCity(city)">{{city.name}}</span>
                      <!-- 这里不太理解 -->
                 </dd>
             </dl>
    </div>
</template>
<script>
import api from '@/api/index.js'
export default {
   data(){
       return {
           list:'ABCDEFGIJKLMNOPQESTUVWXYZ'.split(''),
           cityList:[],
           cityGroup:{}
       }
   },
   created(){
         api.getCityList().then(res=>{
               var obj = {}
           res.data.data.forEach((item,index)=>{
            if(!obj[item.firstChar.toUpperCase()]){
                obj[item.firstChar.toUpperCase()] = [];
            }
            obj[item.firstChar.toUpperCase()].push(item);
           })

           this.cityGroup = obj;
         })

        //   var data=[{
        //        "id":1,
        //        "name":"北京",
        //        "pinyin":"beijing",
        //        "acronym":"bj",
        //        "rank":"S",
        //        "firstChar":"b"
        //    },
        //    {
        //        "id":10,
        //        "name":"上海",
        //        "pinyin":"上海",
        //        "acronym":"sh",
        //        "rank":"S",
        //        "firstChar":"b"
        //    },{
        //        "id":20,
        //        "name":"广州",
        //        "pinyin":"guangzhou",
        //        "acronym":"gz",
        //        "rank":"S",
        //        "firstChar":"g"
        //    },{
        //        "id":30,
        //        "name":"深圳",
        //        "pinyin":"shengzhen",
        //        "acronym":"sz",
        //        "rank":"S",
        //        "firstChar":"s"
        //    }]

   },
   methods:{
      changeCity(item){
          this.$store.dispatch('setPosition',item);
          this.$router.push({name:'index'})
      }
   }
}
</script>
<style lang="scss">

 @import "@/assets/css/changecity/categroy.scss"
</style>


