<template>
  <div class="m-istyle">
    <dl @mouseover="over" :class="nav.class">
      <dt>{{ nav.title }}</dt>
      <dd
        v-for="(item, index) in nav.list"
        :key="index"
        :class="{ active: kind == item.tab }"
        :data-type="item.tab"
      >
        {{ item.text }}
      </dd>
    </dl>
    <ul class="ibody">
      <!-- 什么意思kind -->
      <li v-for="(item, index) in list[kind]" :key="index">
        <el-card :body-style="{ padding: '0px' }" shadow="never">
          <img :src="item.image" class="image" />
          <div class="cbody">
            <div class="title" :title="item.title">{{ item.title }}</div>
            <div class="sub-title" :title="item.sub_title">{{ item.sub_title }} </div>
            
           
            <!-- <div class="price-info" >
              <span class="current-price-wrappper">
                <span class="price-symbol numfont">￥</span>
                <span class="current-price numfont">{{
                  item.price_info.current_price
                }}</span>
              </span>
              <span class="old-price">{{ item.price }}</span>
              <span class="sold bottom-right-info">{{
                item.price_info.address
              }}</span>
            </div> -->
            <!-- <div class="price-info" v-else-if="!item.rentNum">
                  <span class="current-price-wrappper">
                <span class="price-symbol numfont">￥</span>
                <span class="current-price numfont">抢光了</span>
              </span>
            </div> -->
             <div class="price-info" >
                 <span class="current-price-wrappper">
                <span class="price-symbol numfont">￥</span>
                <span class="current-price numfont">{{item.price}}</span>
                <span class="units">/人均</span>
              </span>
             </div>
          </div>
        </el-card>
      </li>
    </ul>
  </div>
</template>
<script>
import api from '@/api/index.js'
export default {
  data() {
    return {
      kind: "all",
      list:{}
    };
  },
  props: ["nav"],
  created(){
      api.resultsByKeywords().then((res)=>{
           this.list=res.data.data;
      })
  },
  methods: {
    over(e) {

      let dom = e.target;
      let tagName = dom.tagName.toLowerCase();
      console.log(tagName);
      if (tagName != "dd") {
        return false;
      }
      this.kind = dom.getAttribute("data-type");
      // 动态获取数据  ajax请求
    },
  },
};
</script>
<style lang="scss">
@import "@/assets/css/index/artistic.scss";
</style>


